.footer {
  background-color: $color-blue-dark;
  padding-top: 95px;
  padding-bottom: 95px;
  width: 100%;
  @media (max-width: 991px) {
    padding: 45px 30px 45px 30px;
  }
  .container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    @media (max-width: 991px) {
      flex-direction: column;
    }
  }
  &__copyright {
    p {
      color: $color-white;
      font-size: 16px;
      line-height: 18px;
      margin-bottom: 0;
      @media (max-width: 991px) {
        font-size: 16px;
        margin-bottom: 24px;
        text-align: center;
      }
      @media (max-width: 575px) {
        font-size: 14px;
      }
      @include font-work-sans-semibold();
    }
  }
  &__menu {
    ul {
      display: flex;
      justify-content: flex-end;
      flex-direction: row;
      list-style-type: none;
      padding: 0;
      margin: 0;
      @media (max-width: 991px) {
        flex-direction: column;
        text-align: center;
      }
      li {
        position: relative;
        margin-left: 50px;
        @media (max-width: 991px) {
          margin-left: 0;
          margin-bottom: 16px;
        }
        &:first-child {
          a {
            margin-left: 0;
          }
        }
        &:last-child {
          @media (max-width: 991px) {
            margin-bottom: 0;
          }
        }
      }
    }
    a {
      width: 104px;
      height: 18px;
      color: #ced1db;
      font-size: 16px;
      letter-spacing: -0.32px;
      @include font-work-sans-medium();
      @media (max-width: 991px) {
        font-size: 15px;
        letter-spacing: -0.3px;
      }
      @media (max-width: 575px) {
        font-size: 14px;
        letter-spacing: -0.28px;
      }
      &:before {
        content: "";
        position: absolute;
        width: 100%;
        height: 1px;
        background-color: $color-white;
        bottom: -6px;
        left: 50%;
        right: 0;
        transform: translate(-50%, 0);
        transition: 0.3s;
        opacity: 0;
      }
      &:hover {
        text-decoration: none !important;
        color: $color-white;
        &:before {
          transform: translate(-50%, -3px);
          opacity: 1;
        }
      }
    }
  }
}



