html {
  overflow-x: hidden;
  width: 100%;
  height: 100%;
}
body {
  position: relative;
  z-index: 1;
  overflow-x: hidden;
}
.cookies {
  background-color: $color-blue-black;
  transition: 0.3s;
  position: relative;
  z-index: 20;
  .container {
    padding-bottom: 25px;
    padding-top: 25px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    @media (max-width: 767px) {
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      padding-bottom: 15px;
      padding-top: 15px;
    }
  }
  p {
    color: #f0f1f7;
    font-size: 14px;
    letter-spacing: -0.28px;
    line-height: 18px;
    margin-bottom: 0;
    max-width: 1026px;
    @include font-work-sans-medium();
    span {
      color: #f0f1f7;
      font-size: 14px;
      @include font-work-sans-bold();
    }
  }
  a {
    color: $color-blue-black;
    border-radius: 50px;
    background-color: $color-white;
    font-size: 16px;
    font-weight: 600;
    letter-spacing: -0.32px;
    display: flex;
    align-items: center;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-right: 30px;
    padding-left: 30px;
    transition: 0.3s;
    max-width: 114px;
    @include font-work-sans-semibold();
    @media (max-width: 767px) {
      margin-top: 20px;
    }
    &:hover {
      text-decoration: none;
    }
  }
}
.header {
  @media (max-width: 991px) {
    position: relative;
    z-index: -1;
  }
  &__container {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    width: 100%;
    margin-top: 10px;
    margin-bottom: 30px;
    @media (max-width: 991px) {
      margin-top: 5px;
    }
    @media (max-width: 767px) {
      align-items: flex-start;
    }
    @media (max-width: 575px) {
      max-height: 85px;
      align-items: center;
    }
  }
  &__logo {
    img {
      width: 100%;
      @media (max-width: 767px) {
        max-width: 131px;
      }
    }
    @media (max-width: 991px) {
      &:after {
        bottom: -25px;
        content: "";
        width: 800px;
        height: 96px;
        display: block;
        position: absolute;
        z-index: -1;
        left: -200px;
        background-color: #f0f1f7;
      }
    }
    @media (max-width: 767px) {
      display: flex;
      width: 100%;
      a {
        display: flex;
        width: 100%;
      }
      img {
        max-width: 131px;
      }
    }
  }
  &__request {
    margin-right: 50px;
    font-size: 16px;
    letter-spacing: -0.12px;
    padding-top: 21px;
    position: relative;
    z-index: 100;
    @media (max-width: 991px) {
      &:after {
        bottom: -75px;
        content: "";
        width: 500px;
        height: 161px;
        display: block;
        position: absolute;
        z-index: -1;
        background-color: $color-blue;
        background-image: url("images/background-blue.png");
        background-repeat: no-repeat;
        left: -20px;
      }
    }
    @media (max-width: 767px) {
      width: 316px;
      font-size: 14px;
      letter-spacing: -0.28px;
      line-height: 16px;
      margin-right: -80px;
    }
    @media (max-width: 575px) {
      max-height: 68px;
      height: 100%;
      display: flex;
      align-items: center;
      bottom: 0;
      padding: 0 !important;
    }
    @include font-work-sans-bold();
    a {
      position: relative;
      color: $color-white;
      @media (max-width: 575px) {
        display: block;
        height: 5px;
      }
      &:before {
        content: "";
        position: absolute;
        width: 100%;
        height: 1px;
        background-color: $color-white;
        bottom: -6px;
        left: 50%;
        right: 0;
        transform: translate(-50%, 0);
        transition: 0.3s;
        opacity: 0;
      }
      &:hover {
        text-decoration: none !important;
        color: $color-white;
        &:before {
          transform: translate(-50%, -3px);
          opacity: 1;
        }
      }
    }
  }
}
.top-content {
  margin-top: -20px;
  background-color: $color-white;
  position: relative;
  z-index: 0;
  &__container {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
  }
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    width: 100%;
    @media (max-width: 991px) {
      flex-direction: column;
    }
  }
  &__text {
    position: relative;
    @media (max-width: 991px) {
      margin-bottom: 66px;
    }
    h1 {
      color: $color-blue-dark;
      font-size: 48px;
      font-weight: 400;
      letter-spacing: -0.96px;
      max-width: 470px;
      @include font-work-sans-bold();
      @media (max-width: 991px) {
        text-align: center;
        font-size: 40px;
        letter-spacing: -0.8px;
        line-height: 40px;
        margin-top: 70px;
      }
      @media (max-width: 576px) {
        font-size: 32px;
        letter-spacing: -0.64px;
        line-height: 36px;
        margin-top: 50px;
      }
      span {
        @include font-librefranklin();
        @media (max-width: 991px) {
          font-size: 40px;
          letter-spacing: -0.8px;
          line-height: 40px;
        }
        @media (max-width: 576px) {
          font-size: 32px;
          letter-spacing: -0.64px;
          line-height: 36px;
        }
      }
    }
    p {
      max-width: 470px;
      color: $color-blue-dark;
      font-size: 18px;
      letter-spacing: -0.36px;
      line-height: 28px;
      @include font-work-sans-regular();
      @media (max-width: 991px) {
        text-align: center;
        font-size: 16px;
        letter-spacing: -0.32px;
        line-height: 24px;
      }
      @media (max-width: 576px) {
        font-size: 14px;
        letter-spacing: -0.28px;
        line-height: 21px;
      }
    }
    a {
      color: $color-blue;
      font-size: 18px;
      letter-spacing: -0.36px;
      line-height: 20px;
      text-decoration: none;
      position: relative;
      overflow: hidden;
      @include font-work-sans-bold();
      @media (max-width: 991px) {
        text-align: center;
        display: block;
        font-size: 17px;
        letter-spacing: -0.32px;
        &:after {
          margin-left: 12px;
        }
      }
      @media (max-width: 576px) {
        font-size: 14px;
        letter-spacing: -0.28px;
        &:after {
          margin-left: 9px;
        }
      }
      &:before {
        content: "";
        position: absolute;
        width: 0;
        height: 2px;
        background-color: $color-blue;
        bottom: -6px;
        left: 0;
        transition: 0.3s;
      }
      &:hover {
        text-decoration: none !important;
        &:before {
          width: 100%;
        }
      }
      &:after {
        margin-left: 16px;
        content: url("images/link-arrow.svg");
      }
    }
  }
}
.form {
  display: flex;
  position: relative;
  justify-content: center;
  flex-direction: column;
  background-color: $color-white;
  max-width: 570px;
  width: 100%;
  padding: 50px;
  box-shadow: $box-shadow;
  border-radius: 6px;
  @media (min-width: 2000px) {
    &:after {
      background-repeat: repeat-x !important;
    }
  }
  @media (max-width: 911px) {
    padding: 37px;
  }
  @media (max-width: 576px) {
    padding: 24px;
  }
  &:after {
    top: -110px;
    bottom: -120px;
    content: "";
    width: 2100px;
    left: 100px;
    height: 150%;
    display: block;
    position: absolute;
    background-color: $color-blue;
    background-image: url("images/background-blue.png");
    background-repeat: no-repeat;
    background-size: contain;
    z-index: -1;
  }
  &:before {
    content: url("images/form-corner.png");
    width: 40px;
    height: 40px;
    position: absolute;
    right: 50px;
    bottom: -40px;
  }
  @media (max-width: 1200px) {
    max-width: 470px;
  }
  @media (max-width: 991px) {
    &:after {
      content: "";
      top: 50px;
      bottom: -100px;
      left: -270px;
      width: 1000px;
      height: auto;
      background-color: $color-blue;
      background-image: url("images/background-blue.png");
      background-repeat: no-repeat;
      background-size: cover;
    }
  }
  h2 {
    text-align: center;
    color: $color-black;
    font-size: 32px;
    letter-spacing: -0.64px;
    @include font-work-sans-bold();
  }
  &__item {
    margin-top: 21px;
    width: 100%;
    label {
      position: absolute;
      margin-top: -6px;
      margin-left: 12px;
      padding-left: 5px;
      padding-right: 5px;
      color: $color-blue-black;
      font-size: 12px;
      line-height: 13px;
      background-color: $color-white;
      @include font-work-sans-medium();
    }
    input,
    textarea {
      width: 100%;
      border-radius: 3px;
      border: 1px solid #ced1db;
      padding: 15px;
      @include font-work-sans-regular();
      outline: none;
      &::placeholder {
        color: $color-grey;
        font-size: 18px;
        letter-spacing: -0.36px;
        line-height: 20px;
      }
    }
  }
  &__span-grey {
    color: #8d95a6;
  }
  &__captcha {
    margin-top: 23px;
    display: flex;
    img {
      margin: 0 auto;
      max-width: 100%;
    }
  }
  &__submit-button {
    width: 100%;
    margin-top: 23px;
    button {
      width: 100%;
      height: 60px;
      box-shadow: 0 5px 20px rgba(250, 57, 80, 0.5);
      border-radius: 50px;
      background-color: $color-red;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      position: relative;
      color: $color-white;
      outline: none;
    }
  }
  &__checkbox {
    margin-top: 21px;
    color: $color-blue-dark;
    font-size: 16px;
    letter-spacing: -0.32px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    @include font-work-sans-regular();
    input {
      opacity: 0;
      & + label {
        position: relative;
        cursor: pointer;
        padding: 0;
      }
      & + label:before {
        content: "";
        display: inline-block;
        vertical-align: text-top;
        width: 34px;
        height: 34px;
        background: $color-white;
        border-radius: 3px;
        border: 1px solid #ced1db;
        background-color: $color-white;
      }
      &:checked + label:after {
        content: url("images/mark-red.svg");
        position: absolute;
        left: 7px;
        top: 10px;
        width: 32px;
        height: 32px;
      }
      @media (max-width: 450px) {
        margin-left: -17px;
      }
    }
    label {
      display: flex;
      flex-direction: row;
      align-items: center;
      margin-bottom: 0;
      span {
        margin-left: 20px;
        margin-right: 5px;
        @media (max-width: 450px) {
          margin-left: 5px;
        }
      }
    }
    a {
      color: $color-red;
      text-decoration: underline;
    }
  }
  &__success {
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 6px;
    background-color: $color-white;
    z-index: -1;
    transition: 0.3s;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px;
    text-align: center;
    @media (max-width: 767px) {
      padding: 20px;
    }
    h2 {
      margin-bottom: 0;
    }
    p {
      max-width: 470px;
      color: $color-blue-dark;
      font-size: 18px;
      letter-spacing: -0.36px;
      line-height: 28px;
      margin-top: 16px;
      margin-bottom: 68px;
      @include font-work-sans-regular();
    }
    &__active {
      opacity: 1;
      z-index: 100;
    }
  }
  &__or {
    margin-top: 21px;
    margin-bottom: 28px;
    height: 18px;
    color: $color-grey;
    font-size: 16px;
    letter-spacing: -0.32px;
    @include font-work-sans-regular();
  }
  &__envelope {
    background-color: #f0f1f7;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    img {
      margin-top: -12px;
      width: 114px;
      height: 110px;
    }
  }
}
.hit-works {
  margin-top: 120px;
  position: relative;
  z-index: 20;
  @media (max-width: 991px) {
    margin-top: 100px;
  }
  @media (max-width: 767px) {
    margin-top: 50px;
  }
  &__container {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    position: relative;
    @media (max-width: 767px) {
      flex-direction: column;
    }
    &:before {
      content: "";
      top: 0;
      bottom: 0;
      width: 2000px;
      left: 217px;
      height: 100%;
      display: block;
      position: absolute;
      z-index: -1;
      background-color: #f0f1f7;
      @media (max-width: 767px) {
        background-color: $color-white;
        width: 0;
      }
    }
  }
  &__image {
    @media (max-width: 767px) {
      max-width: 220px;
    }
    @media (max-width: 576px) {
      max-width: 170px;
    }
    img {
      width: auto;
      @media (max-width: 767px) {
        width: 100%;
        z-index: 50;
        position: relative;
      }
    }
  }
  &__content {
    width: 100%;
    padding: 150px 0 150px 79px;
    @media (max-width: 991px) {
      padding-left: 49px;
    }
    @media (max-width: 767px) {
      padding: 50px 20px 48px 20px;
      text-align: center;
    }
    h2 {
      margin-bottom: 24px;
      color: $color-blue-dark;
      font-size: 48px;
      letter-spacing: -1.92px;
      @include font-work-sans-medium();
      @media (max-width: 767px) {
        font-size: 40px;
        letter-spacing: -1.6px;
        line-height: 42px;
      }
      @media (max-width: 575px) {
        font-size: 32px;
        letter-spacing: -1.28px;
        line-height: 36px;
      }
    }
    p {
      width: 100%;
      max-width: 670px;
      color: $color-blue-dark;
      font-size: 18px;
      letter-spacing: -0.36px;
      line-height: 28px;
      @include font-work-sans-regular();
      @media (max-width: 767px) {
        font-size: 16px;
        letter-spacing: -0.32px;
        line-height: 25px;
      }
      @media (max-width: 575px) {
        font-size: 14px;
        letter-spacing: -0.28px;
        line-height: 21px;
      }
    }
  }
  &__button {
    display: flex;
    width: fit-content;
    max-width: 230px;
    justify-content: center;
    margin-top: 22px;
    align-items: center;
    color: $color-blue-dark;
    font-size: 18px;
    letter-spacing: -0.36px;
    line-height: 20px;
    padding: 22px 26px 22px 26px;
    border-radius: 50px;
    border: 2px solid $color-blue-dark;
    text-decoration: none !important;
    transition: 0.3s;
    @include font-work-sans-bold();
    @media (max-width: 767px) {
      margin: 0 auto;
    }
    &:before {
      content: url("images/play.svg");
      margin-right: 13px;
    }
    &:hover {
      background-color: $color-blue-dark;
      color: $color-white;
      &:before {
        content: url("images/play-grey.svg");
      }
    }
  }
}



