.red-button {
  width: 100%;
  height: 60px !important;
  box-shadow: 0 5px 20px rgba(250, 57, 80, 0.5);
  border-radius: 50px;
  background-color: $color-red;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: relative;
  color: $color-white;
  outline: none;
  font-size: 18px;
  letter-spacing: -0.36px;
  transition: 0.3s;
  border: none;
  @include font-work-sans-semibold();
  &:hover {
    text-decoration: none;
    color: $color-white;
    background-color: $color-red-hover;
  }
}



