.info {
  .header {
    @media (max-width: 991px) {
      position: relative;
      z-index: 0;
    }
    &__container {
      position: relative;
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-direction: row;
      width: 100%;
      margin-top: 10px;
      margin-bottom: 50px;
      @media (max-width: 991px) {
        margin-top: 5px;
      }
      @media (max-width: 767px) {
        align-items: flex-start;
        margin-bottom: 30px;
      }
    }
    &__logo {
      img {
        width: 100%;
        @media (max-width: 767px) {
          max-width: 131px;
        }
      }
      @media (max-width: 991px) {
        &:after {
          bottom: -25px;
          content: "";
          width: 800px;
          height: 96px;
          display: block;
          position: absolute;
          z-index: -1;
          left: -200px;
          background-color: transparent;
        }
      }
      @media (max-width: 767px) {
        display: flex;
        width: 100%;
        a {
          display: flex;
          width: 100%;
        }
        img {
          max-width: 131px;
        }
      }
      @media (max-width: 575px) {
        &:after {
          background-color: #f0f1f7;
        }
      }
    }
    &__request {
      margin-right: 50px;
      font-size: 16px;
      letter-spacing: -0.12px;
      padding-top: 21px;
      position: relative;
      z-index: 100;
      @media (max-width: 991px) {
        &:after {
          display: none;
        }
      }
      @media (max-width: 575px) {
        &:after {
          bottom: -40px;
          content: "";
          width: 500px;
          height: 161px;
          display: block;
          position: absolute;
          z-index: -1;
          background-color: $color-blue;
          background-image: url("images/background-blue.png");
          background-repeat: no-repeat;
          left: -20px;
        }
      }
      @media (max-width: 575px) {
        width: 316px;
        font-size: 14px;
        letter-spacing: -0.28px;
        line-height: 16px;
        margin-right: -80px;
      }
      @include font-work-sans-bold();
      a {
        position: relative;
        color: $color-white;
        &:before {
          content: "";
          position: absolute;
          width: 100%;
          height: 1px;
          background-color: $color-white;
          bottom: -6px;
          left: 50%;
          right: 0;
          transform: translate(-50%, 0);
          transition: 0.3s;
          opacity: 0;
        }
        &:hover {
          text-decoration: none !important;
          color: $color-white;
          &:before {
            transform: translate(-50%, -3px);
            opacity: 1;
          }
        }
      }
    }
  }
}

.text-page {
  margin-bottom: 100px;
  margin-top: -20px;
  @media (max-width: 767px) {
    margin-bottom: 75px;
  }
  @media (max-width: 575px) {
    margin-bottom: 50px;
  }
  h1 {
    text-align: center;
    color: $color-black;
    font-size: 32px;
    letter-spacing: -0.64px;
    margin-bottom: 20px;
    @include font-work-sans-bold();
  }
  p,
  li {
    width: 100%;
    color: $color-blue-dark;
    font-size: 18px;
    letter-spacing: -0.36px;
    line-height: 28px;
    @include font-work-sans-regular();
    @media (max-width: 767px) {
      font-size: 16px;
      letter-spacing: -0.32px;
      line-height: 25px;
    }
    @media (max-width: 575px) {
      font-size: 14px;
      letter-spacing: -0.28px;
      line-height: 21px;
    }
  }
  b {
    color: $color-blue-black;
    font-size: 18px;
    letter-spacing: -0.36px;
    line-height: 28px;
    @include font-work-sans-semibold();
    @media (max-width: 767px) {
      font-size: 16px;
      letter-spacing: -0.32px;
      line-height: 25px;
    }
    @media (max-width: 575px) {
      font-size: 14px;
      letter-spacing: -0.28px;
      line-height: 21px;
    }
  }
  a {
    color: $color-red;
    text-decoration: none;
    &:hover {
      color: $color-red-hover;
      text-decoration: none;
    }
  }
  &__container {
    display: flex;
    position: relative;
    justify-content: center;
    flex-direction: column;
    background-color: $color-white;
    width: 100%;
    padding: 50px;
    box-shadow: $box-shadow;
    border-radius: 6px;
    @media (max-width: 911px) {
      padding: 37px;
    }
    @media (max-width: 575px) {
      padding: 24px;
      border-radius: 0;
    }
    &:after {
      top: -110px;
      content: "";
      width: 1200px;
      left: 650px;
      bottom: -100px;
      display: block;
      position: absolute;
      background-color: $color-blue;
      background-image: url("images/background-blue.png");
      z-index: -1;
    }
    @media (max-width: 991px) {
      &:after {
        left: 350px;
      }
    }
    @media (max-width: 767px) {
      &:after {
        left: 250px;
      }
    }
    @media (max-width: 575px) {
      &:after {
        content: "";
        top: 0;
        bottom: -100px;
        left: -270px;
        background-color: $color-blue;
      }
    }
  }
}



